<template>
  <div>
<el-upload
  :disabled="disabled"
  class="upload-demo"
  action="../api/upms/file/uploadFile"
  :on-preview="handlePictureCardPreview"
  :before-remove="beforeRemove"
  :on-change="onChange"
  :on-success="onSuccess"
  :on-error="onError"
  :on-remove="onRemove"
  :file-list="fileList"
  list-type="picture"
  :multiple = "multiple"
  :headers="fileheader">
  <el-button size="small" type="primary" v-if="!disabled" >点击上传</el-button>
  <!--<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>-->
</el-upload>
  <el-dialog :visible.sync="dialogVisible" center append-to-body  width="50%">
    <img  @click="dialogVisible = false" style="max-width: 100%;max-height: 100%" :src="dialogImageUrl" alt="">
  </el-dialog>
  </div>
</template>
<script>
import {base64Pic, downloadFile} from '@/api'
export default {
  name: 'upload',
  /**
   * fileArray 默认显示文件列表[{name: 'food.jpeg', url: '/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg'}, {name: 'food2.jpeg', url: '/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg'}]
   * isDisabled 是否显示“点击上传” 按钮 true为显示 false不显示 默认不显示
   * multiple 是否为批量上传 默认单个上传 ,true为批量上传
   */
  props: ['fileArray', 'isDisabled', 'ismultiple'],
  data () {
    return {
      fileheader: {'Authorization': sessionStorage.token},
      dialogVisible: false,
      dialogImageUrl: '',
      fileList: [],
      disabled: false,
      multiple: false
    }
  },
  watch: {
    fileArray: function (val) {
      this.fileList = []
      if (val != null && val !== undefined) {
        val.forEach(file => {
          var urls = file.url.split('.')
          file.path = file.url
          if (urls[1].toLocaleLowerCase() === 'png' || urls[1].toLocaleLowerCase() === 'jpg' || urls[1].toLocaleLowerCase() === 'jpeg' || urls[1].toLocaleLowerCase() === 'gif' || urls[1].toLocaleLowerCase() === 'bmp') {
            base64Pic({'fileName': file.url}).then((response) => {
              file.url = response.data
              this.fileList.push(this.$common.deepClone(file))
            }).catch((response) => {
            })
          } else {
            this.fileList.push(this.$common.deepClone(file))
          }
        })
      }
    },
    isDisabled (val) {
      if (val != null && val !== undefined) {
        this.multiple = val
      } else {
        this.multiple = false
      }
    },
    ismultiple (val) {
      if (val != null && val !== undefined) {
        this.disabled = val
      } else {
        this.disabled = false
      }
    }
  },
  created: function () {
    if (this.isDisabled != null && this.isDisabled !== undefined) {
      this.disabled = this.isDisabled
    } else {
      this.disabled = false
    }

    if (this.ismultiple != null && this.ismultiple !== undefined) {
      this.multiple = this.ismultiple
    } else {
      this.multiple = false
    }
  },
  methods: {
    handlePictureCardPreview (file) {
      // 下载文件
      var names = file.name.split('.')
      if (names[1].toLocaleLowerCase() === 'png' || names[1].toLocaleLowerCase() === 'jpg' || names[1].toLocaleLowerCase() === 'jpeg' || names[1].toLocaleLowerCase() === 'gif' || names[1].toLocaleLowerCase() === 'bmp') {
        this.dialogImageUrl = file.url
        this.dialogVisible = true
      } else {
        downloadFile({'fileName': file.url}).then((response) => {
          this.$common.download(response.data, file.name)
        }).catch((response) => {
          this.$message({
            showClose: true,
            message: '下载失败！',
            type: 'error'
          })
        })
      }
    },
    beforeRemove (file, fileList) {
      return this.$confirm('确定移除 ' + file.name + '？')
    },
    onChange (file, fileList) {
      this.fileList = fileList
      this.$emit('child-event', this.fileList)
    },
    onRemove (file, fileList) {
      this.fileList = fileList
      this.$emit('child-event', this.fileList)
    },
    onSuccess (response, file, fileList) {
      if (response.result === '004') {
        this.$message({
          showClose: true,
          message: '用户登录超时！',
          type: 'error'
        })
        this.$router.push({name: 'home', params: {}})
        return
      }
      for (var index in fileList) {
        if (fileList[index]['response'] != null && fileList[index]['response'] !== undefined) {
          fileList[index]['path'] = fileList[index]['response'][0]['newfilename']
          var urls = fileList[index]['path'].split('.')
          if (urls[1].toLocaleLowerCase() === 'png' || urls[1].toLocaleLowerCase() === 'jpg' || urls[1].toLocaleLowerCase() === 'jpeg' || urls[1].toLocaleLowerCase() === 'gif' || urls[1].toLocaleLowerCase() === 'bmp') {
            base64Pic({'fileName': fileList[index]['path']}).then((response) => {
              fileList[index].url = response.data
            }).catch((response) => {
            })
          } else {
            fileList[index].url = fileList[index]['response'][0]['newfilename']
          }
          fileList[index].response = null
        }
      }
      this.fileList = fileList
    },
    onError (response, file, fileList) {
    }
  }
}
</script>



