<template>
  <div>
    <div class="app-container-item">

      <div class="app-form" v-show="showQuery">
        <div class="app-form-item">
          <el-input type="text" v-model="tableOptions.query.name" placeholder="输入系统名称搜索" style="width: 200px;"/>
        </div>
        <div class="app-form-item">
          <el-select v-model="tableOptions.query.status" placeholder="请选择状态" clearable style="width: 120px;">
            <el-option
              v-for="item in statusTypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="app-form-buttons">
          <div class="app-form-buttons-item">
            <el-button @click="loadData" type="primary">
              <ant-icon icon-class="SearchOutlined"></ant-icon>
              查询
            </el-button>
          </div>
          <div class="app-form-buttons-item">
            <el-button @click="clear" type="success">
              <ant-icon icon-class="RedoOutlined"></ant-icon>
              重置
            </el-button>
          </div>
        </div>
      </div>

      <div class="app-hr" v-show="showQuery"></div>

      <div class="app-form">
        <div class="app-form-buttons">
          <div class="app-form-buttons-item" v-buttonshow="'upms:System:add'">
            <el-button @click="addSystem" type="success">
              <ant-icon icon-class="PlusOutlined"></ant-icon>
              新增
            </el-button>
          </div>
          <div class="app-form-buttons-item" v-buttonshow="'upms:System:upd'">
            <el-button @click="editSystem" type="danger">
              <ant-icon icon-class="EditOutlined"></ant-icon>
              修改
            </el-button>
          </div>
          <div class="app-form-buttons-item" v-buttonshow="'upms:System:del'">
            <el-button @click="delBeforeSystemButton" type="warning">
              <ant-icon icon-class="DeleteOutlined"></ant-icon>
              删除
            </el-button>
          </div>
          <div class="app-form-buttons-item" v-buttonshow="'upms:System:enable'">
            <el-button @click="openSystemButton" type="warning">
              <ant-icon icon-class="UploadOutlined"></ant-icon>
              启用
            </el-button>
          </div>
          <div class="app-form-buttons-item" v-buttonshow="'upms:System:unable'">
            <el-button @click="closeSystemButton" type="warning">
              <ant-icon icon-class="UploadOutlined"></ant-icon>
              禁用
            </el-button>
          </div>
          <div class="app-form-buttons-item">
            <el-button @click="expSystem" type="warning">
              <ant-icon icon-class="UploadOutlined"></ant-icon>
              导出
            </el-button>
          </div>
        </div>
        <el-button-group class="app-form-button-group align-right">
          <el-button
            plain
            type="info"
            icon="el-icon-search"
            @click="toggleQuery"
          />
          <el-button
            icon="el-icon-refresh"
            @click="refresh"
          />
        </el-button-group>
      </div>

    </div>

    <div class="app-container-item">
      <TableBase v-bind="tableOptions" ref="tableref"></TableBase>
    </div>

    <el-dialog
      title="系统编辑"
      :visible.sync="centerDialogVisible"
      width="480px"
      >
      <el-form :model="systemObj" :rules="rules" label-suffix="" ref="ruleForm" label-width="120px" class="demo-ruleForm">
        <el-form-item label="系统标题" prop="title">
          <el-input v-model="systemObj.title" placeholder="系统标题"></el-input>
        </el-form-item>
        <el-form-item label="系统名称" prop="name">
          <el-input v-model="systemObj.name" placeholder="系统名称"></el-input>
        </el-form-item>
        <el-form-item label="根目录" prop="basepath">
          <el-input v-model="systemObj.basepath" placeholder="根目录"></el-input>
        </el-form-item>
        <el-form-item label="图标" prop="icon">
          <upload :fileArray="fileArray" @child-event="setFile"></upload>
          <!--<el-input v-model="systemObj.icon" placeholder="图标" ></el-input>-->
        </el-form-item>
        <el-form-item label="系统描述" prop="description">
          <el-input
            type="textarea"
            :rows="3"
            placeholder="系统描述"
            v-model="systemObj.description">
          </el-input>
        </el-form-item>
        <el-form-item label="排序" prop="orders">
          <el-input-number v-model="systemObj.orders" :min="1"></el-input-number>
        </el-form-item>
        <!--<el-form-item label="状态" prop="status">-->
        <!--<el-radio v-model="systemObj.status" :label="'0'">正常</el-radio>-->
        <!--<el-radio v-model="systemObj.status" :label="'1'">禁用</el-radio>-->
        <!--</el-form-item>-->
        <el-form-item label="系统主题" prop="theme">
          <el-input v-model="systemObj.theme" placeholder="系统主题"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveSystemObj">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import TableBase from '@/components/table/TableBase'
import upload from '@/components/base/upload'
import {addSystem, delSystem, editSystem, querySystem, systemOpenOrClose} from '@/api'
export default {
  components: {TableBase, upload},
  name: 'System',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 查询是否显示
      showQuery: true,
      statusTypes: [{
        value: '0',
        label: '正常'
      }, {
        value: '1',
        label: '禁用'
      }, {
        value: '2',
        label: '系统禁用'
      }],
      // 注释规则： //
      fileArray: [],
      // 填写验证
      rules: {
        name: [{ required: true, message: '系统名称不能为空', trigger: 'blur' }],
        orders: [{ required: true, message: '排序不能为空', trigger: 'blur' }],
        title: [{ required: true, message: '系统标题不能为空', trigger: 'blur' }],
        basepath: [{ required: true, message: '系统根目录不能为空', trigger: 'blur' }]
      },
      // 修改状态
      objStatus: false,
      // 系统对象
      systemObj: {
      },
      // 编辑弹框显示隐藏
      centerDialogVisible: false,
      tableOptions: {
        api: querySystem,
        hideOnSinglePage: false,
        staticData: [],
        // 查询对象
        query: {
          name: ''
        },
        columns: [
          {
            attr: {
              prop: 'id',
              type: 'selection',
              label: '多选',
              width: '80'
            }
          },
          {
            attr: {
              type: 'index',
              label: '序号',
              width: '80'
            }
          },
          {
            attr: {
              prop: 'name',
              label: '系统名称',
              'min-width': '140'
            }
          },
          {
            attr: {
              prop: 'title',
              label: '系统标题',
              'min-width': '140'
            }
          }, {
            attr: {
              prop: 'basepath',
              label: '根目录'
            }
          },
          {
            attr: {
              prop: 'status',
              label: '状态',
              width: '180',
              formatter (row, column, cellValue, index) {
                let val = '正常'
                if (cellValue === '1') {
                  val = '禁用'
                }
                return val
              }
            }
          },
          {
            attr: {
              prop: 'operation',
              label: '操作',
              width: 100,
              fixed: 'right'
            },
            template: [
              {
                el: 'el-button',
                attr: {
                  type: 'text'
                },
                attrByRow: {
                  disabled: function (index, row) {
                    if (row.date === '2016-05-04') {
                      return true
                    }
                  }
                },
                content: '修改',
                handle: (index, row) => {
                  this.editSystemRadio(row)
                }
              },
              {
                el: 'el-button',
                attr: {
                  type: 'text'
                },
                content: '删除',
                handle: (index, row) => {
                  this.delBeforeRadio(row)
                }
              }
            ]
          }
        ]
      }
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
  },
  // 页面创建加载方法
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.loadData()
    })
  },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    /**
     * @desc 控制查询的显隐
     */
    toggleQuery () {
      this.showQuery = !this.showQuery
    },
    /**
     * @desc 刷新表格
     */
    refresh () {
      this.$refs.tableref.refresh()
    },
    clear () {
      this.tableOptions.query = {}
    },
    // 设置上传图片
    setFile (filelist) {
      var filepath = []
      if (filelist != null && filelist.length > 0) {
        for (var index in filelist) {
          var o = {}
          o.url = filelist[index]['path']
          o.name = filelist[index]['name']
          filepath.push(o)
        }
      }
      this.systemObj.icon = JSON.stringify(filepath)
    },
    openSystemButton () {
      let list = this.$refs.tableref.getSelectData()
      if (list.length === 0) {
        this.$message({
          showClose: true,
          message: '请选择要启用的系统！',
          type: 'warning'
        })
        return
      }
      for (let index = 0; index < list.length; index++) {
        if (list[index].status === '0') {
          this.$message({
            showClose: true,
            message: '系统名称为 ' + list[index].name + ' 正常，请不要重复启用！',
            type: 'warning'
          })
          return
        }
      }
      this.$confirm('此操作将启用选择系统, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        systemOpenOrClose({status: '0', listSystemPo: list}).then((response) => {
          if (response.data.code === 0) {
            this.$message({
              showClose: true,
              message: '启用成功',
              type: 'success'
            })
            this.loadData()
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: 'error'
            })
          }
        }).catch((response) => {
        })
      })
    },
    closeSystemButton () {
      let list = this.$refs.tableref.getSelectData()
      if (list.length === 0) {
        this.$message({
          showClose: true,
          message: '请选择要禁用的记录！',
          type: 'warning'
        })
        return
      }
      for (let index = 0; index < list.length; index++) {
        if (list[index].status !== '0') {
          this.$message({
            showClose: true,
            message: '系统名称为 ' + list[index].name + ' 已禁用，请不要重复禁用！',
            type: 'warning'
          })
          return
        }
      }
      this.$confirm('此操作将禁用选择权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        systemOpenOrClose({status: '1', listSystemPo: list}).then((response) => {
          if (response.data.code === 0) {
            this.$message({
              showClose: true,
              message: '禁用成功',
              type: 'success'
            })
            this.loadData()
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: 'error'
            })
          }
        }).catch((response) => {
        })
      })
    },
    loadData () {
      this.$refs.tableref.refresh()
    },
    /**
     * 增加系统
     */
    addSystem () {
      this.systemObj = {
        status: '0',
        orders: 1
      }
      this.objStatus = false
      this.fileArray = []
      this.centerDialogVisible = true
    },
    editSystemRadio (row) {
      this.objStatus = true
      this.systemObj = row
      this.fileArray = []
      if (this.systemObj.icon != null && this.systemObj.icon !== undefined && this.systemObj.icon !== '') {
        this.fileArray = JSON.parse(this.systemObj.icon)
      }
      this.centerDialogVisible = true
    },
    /**
     * 修改系统
     */
    editSystem () {
      let list = this.$refs.tableref.getSelectData()
      if (list.length === 0) {
        this.$message({
          showClose: true,
          message: '请选择要修改的记录！',
          type: 'warning'
        })
        return
      }
      if (list.length > 1) {
        this.$message({
          showClose: true,
          message: '修改只可选择一条记录！',
          type: 'warning'
        })
        return
      }
      for (let index = 0; index < list.length; index++) {
        if (list[index].status !== '0') {
          this.$message({
            showClose: true,
            message: '请先启用' + list[index].name + '，再修改！',
            type: 'warning'
          })
          return
        }
      }
      this.objStatus = true
      this.systemObj = list[0]
      this.fileArray = []
      if (this.systemObj.icon != null && this.systemObj.icon !== undefined && this.systemObj.icon !== '') {
        this.fileArray = JSON.parse(this.systemObj.icon)
      }
      this.centerDialogVisible = true
    },
    delBeforeRadio (row) {
      this.$confirm('此操作将永久删除选择记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let list = []
        list.push(row)
        this.deleteSystem(list)
      })
    },
    /**
     * 删除系统
     */
    delBeforeSystemButton () {
      let list = this.$refs.tableref.getSelectData()
      if (list.length === 0) {
        this.$message({
          showClose: true,
          message: '请选择要修改的记录！',
          type: 'warning'
        })
        return
      }
      this.$confirm('此操作将永久删除选择记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteSystem(list)
      })
    },
    deleteSystem (list) {
      delSystem({listSystemPo: list}).then((response) => {
        if (response.data.code === 0) {
          this.$message({
            showClose: true,
            message: '删除成功！',
            type: 'success'
          })
          this.centerDialogVisible = false
          this.loadData()
        } else {
          this.$message({
            showClose: true,
            message: response.data.message,
            type: 'error'
          })
        }
      }).catch((response) => {
        console.log(response)
        this.$message({
          showClose: true,
          message: '删除失败！',
          type: 'error'
        })
      })
    },
    /**
     * 修改系统
     */
    expSystem () {
    },
    /**
     * 提交系统对象
     */
    saveSystemObj () {
      this.$refs.ruleForm.validate((valid) => {
        if (!valid) {
          return false
        }
        const loadObj = this.$loading({
          lock: true,
          text: '正在加载...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        if (this.objStatus) {
          editSystem(this.systemObj).then((response) => {
            loadObj.close()
            if (response.data.code === 0) {
              this.$message({
                showClose: true,
                message: '保存成功！',
                type: 'success'
              })
              this.centerDialogVisible = false
              this.loadData()
            } else {
              this.$message({
                showClose: true,
                message: response.data.message,
                type: 'error'
              })
            }
          }).catch((response) => {
            console.log(response)
            loadObj.close()
            this.$message({
              showClose: true,
              message: '保存失败！',
              type: 'error'
            })
          })
        } else {
          addSystem(this.systemObj).then((response) => {
            loadObj.close()
            if (response.data.code === 0) {
              this.$message({
                showClose: true,
                message: '保存成功！',
                type: 'success'
              })
              this.centerDialogVisible = false
              this.loadData()
            } else {
              this.$message({
                showClose: true,
                message: response.data.message,
                type: 'error'
              })
            }
          }).catch((response) => {
            console.log(response)
            loadObj.close()
            this.$message({
              showClose: true,
              message: '保存失败！',
              type: 'error'
            })
          })
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



